.upgraded-hero-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding-left: 5vw;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    color: white;
  }
  
  .fade-in {
    opacity: 0;
    animation: fadeInUp 1s ease forwards;
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(30px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .hero-subtext {
    font-size: 1.5rem;
    color: #ccc;
    margin: 20px 0;
    font-family: "Lato", serif;
    font-weight: 300;
  }
  
  .highlight-text {
    color: #00e5ff;
    font-weight: 600;
  }
  
  .cta-buttons {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .hero-btn {
    padding: 12px 24px;
    border-radius: 30px;
    font-size: 1rem;
    text-decoration: none;
    transition: all 0.3s ease;
    font-weight: 500;
    border: 2px solid white;
  }
  
  .hero-btn.primary {
    background: #00e5ff;
    color: black;
    border: 2px solid #00e5ff;
  }
  
  .hero-btn.primary:hover {
    background: transparent;
    color: #00e5ff;
  }
  
  .hero-btn.secondary {
    background: transparent;
    color: white;
  }
  
  .hero-btn.secondary:hover {
    background: white;
    color: black;
  }
  