/* Ensure body and html take full height */
html, body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: "Lato", serif;
  background-color: black;
  overflow-x: hidden; /* Disable horizontal scrolling */
  overflow-y: auto; /* Enable vertical scrolling */
  
}
section {
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh;
  width: 100vw;
  position: relative;
  overflow: visible; /* Allow scrolling */
}
canvas {
  position: absolute; /* Ensure canvas stays behind the content - Title backgorund */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.container {
  position: relative;
  overflow: hidden;
  margin-top: 80px;
}
.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.hero .hero-content {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 5vw;
  transform: translateY(-50%);
  z-index: 2;
  color: white;
  text-align: left;
  transition: transform 0.5s ease, opacity 0.5s ease; /* Smooth animation for content */
  background: transparent;
  
}

.first-line {
  font-size: 8em;
  margin-bottom: 0;
  position: relative;
  top: 130px; /* Adjust this value to move up/down */
  left:1%;
  font-family: "Libre Caslon Display", serif;
  font-weight: 400;
  font-style: normal;
}

.second-line {
  font-size: 7em;
  margin-bottom: 0;
  position: relative;
  top: 5px; /* Adjust this value to move up/down */
  left: 1%;
  font-family: "Lato", serif;
  font-weight: 900;
  font-style: normal;
}

.subheading {
  font-size: 20px;
  line-height: 1.5; /* Ensures proper spacing between lines */
  width: 100%; /* Adjust width so the text doesn't stretch across the whole page */
  margin-bottom: 0;
  position: relative;
  bottom: 170px; /* Adjust this value to move up/down */
  left: 2%;
  font-family: "Lato", serif;
  font-weight: 400;
  font-style: normal;
}

  
/* Hide scrollbar initially */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

/* Show scrollbar when body has class */
body.show-scrollbar ::-webkit-scrollbar {
  opacity: 1;
}

/* Track */
::-webkit-scrollbar-track {
  background: #000;
  border-radius: 10px;
}

/* Thumb */
::-webkit-scrollbar-thumb {
  background: rgba(200, 200, 200, 0.8);
  border-radius: 50px;
  border: 2px solid #000;
}

/* Thumb hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.9);
}

